<template>
  <div class="main">
    <div class="header">
      <Logo class="logo"/>
      <a class="link" href="/auth">Zaloguj si</a>
    </div>
    <div class="info">
      Uwaga! Znajdujesz si w witrynie rodowiska prezentacyjnego.
    </div>
    <div class="info">
      <h1 class="info__title">Funkcje</h1>
      <div class="info__el">
        <div>Zalecenia, nadchodzce wizyty, mapa gabinetw</div>
        <img src="/static/img/preview/patient-home.png" alt="Zalecenia, nadchodzce wizyty, mapa gabinetw" />
      </div>
      <div class="info__el">
        <div>Historia wizyt, kartoteka pacjenta</div>
        <img src="/static/img/preview/patient-appointments-history.png" alt="Historia wizyt, kartoteka pacjenta" />
      </div>
      <div class="info__el">
        <div>Zarzdzanie terminarzem, pacjentami i pracownikami gabinetw</div>
        <img src="/static/img/preview/doctor-offices.png" alt="Zarzdzanie terminarzem, pacjentami i pracownikami gabinetw" />
      </div>
      <div class="info__el">
        <div>Panel specjalisty</div>
        <img src="/static/img/preview/doctor-home.png" alt="Panel specjalisty" />
      </div>
    </div>
  </div>
</template>

<script>
import MainBtn from '../../components/ui/basic/MainBtn'
import Logo from '../../components/ui/basic/logo/Logo'

export default {
  name: 'Main',
  components: {
    MainBtn,
    Logo
  }
}
</script>

<style lang="scss" scoped>
  @import "../../main";

  .main {
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: 100%;
    padding: 2rem;
    max-width: 1200px;
    margin: auto;

    @media only screen and (max-width: 960px) {
      padding: 1rem;
      grid-gap: 1rem;
    }
  }

  .logo {
    @media only screen and (max-width: 960px) {
      font-size: 1rem !important;

      & /deep/ {
        span {
          margin: 0 5px !important;
        }
      }
    }
  }

  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    background: $darkviolet;
    background: linear-gradient(to right, $lightviolet, $darkviolet);
    margin: auto;
    border-radius: 0.5em;
    width: 100%;
    box-sizing: border-box;

    @media only screen and (max-width: 960px) {
      padding: 1rem;
    }
  }

  .info {
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: 100%;
    padding: 2rem;
    border-radius: 0.5em;
    box-shadow: 0 0 20px 0px rgba(213, 213, 213, 0.3);
    background: #fafafc;
    color: $darkviolet;
    font-weight: 600;
    text-align: center;

    @media only screen and (max-width: 960px) {
      padding: 1rem;
      grid-gap: 1rem;
    }

    &__title {
      font-size: 2rem;
      margin: 1rem 0;

      @media only screen and (max-width: 960px) {
        font-size: 1.25rem;
        grid-gap: 0.5rem;
      }
    }

    &__el {
      box-shadow: 0 0 20px 0px rgba(213, 213, 213, 0.3);
      border-radius: 0.5rem;
      overflow: hidden;

      img {
        width: 100%;
      }

      div {
        font-size: 1.5rem;
        padding: 2rem;
        box-sizing: border-box;
        color: #3e3e45;
        border-bottom: 1px solid $lightgrey;

        @media only screen and (max-width: 960px) {
          font-size: 1rem;
          padding: 1rem;
        }
      }
    }
  }

  .link {
    @extend %text--center;
    padding: 1em;
    color: #fafafc;
    font-weight: 600;
    border: 3px solid #fafafc;
    font-size: 1rem;
    transition: 0.2s ease-in-out;
    border-radius: 0.5rem;
    cursor: pointer;

    &:hover {
      background: #fafafc;
      color: $darkviolet;
    }

    @media only screen and (max-width: 960px) {
      padding: 0.75rem;
    }
  }
</style>



