<template>
  <select class="select--main">
    <slot></slot>
  </select>
</template>

<script>
export default {
  name: 'MainSelect'
}
</script>

<style lang="scss" scoped>
  @import "../../../main.scss";

  select::-ms-expand {
    display: none;
  }

  select.select--main {
    background-image: url(data:image/svg+xml;base64,);
    background-position: 95% 50%;
    background-repeat: no-repeat;
    background-color: #f3f4f9;
    background-size: 1em;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: "";
    padding: 5%;
    border: none;
    border-radius: 0.5em;
    display: inline-block;
    color: #3e3e45;
    font-weight: 600;
    position: relative;
    cursor: pointer;
    transition: 0.2s ease-in-out;

    &:hover {
      background-color: #eaeaea;
    }

    div {
      margin: 0;
      font-weight: 600;
    }
  }
</style>



